import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "@/assets/scss/reset.scss";
import { getUserSearchParams } from "@/utils/index";
import { postUserId } from "@/api/user.js";
import { AppService } from '@/services/AppStorageService.js'

async function bootstrap() {
  const app = createApp(App);
  app.use(router);
  app.use(store);
  app.mount("#app");
}
const appid = AppService() === '' ? import.meta.import.meta.env.VITE_CORP_ID : AppService()
const redirect_uri = encodeURIComponent(window.location.href.split("#")[0]);
const AuthUrl = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirect_uri}&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect`;
const code = getUserSearchParams("code");
if (code) {
  // 说明此时已经获取到code
  // 发送code给后端 获取用户的相信信息
  postUserId(code).then(() => {
    // 接口获取成功后才能初始化项目
    //保存到localstorystage里
    void bootstrap();
  }).catch(()=> {
    // 没有授权
  })
} else {
  // 说明此时第一次进入还没有跳转回调地址
  window.location.replace(AuthUrl);
}
